<template lang="pug">
.contentContainer
    h1.TitleU {{name}}
    h2.subTitle Keynote Speakers
    .speakerContainer(v-for="speaker in speakers[year]" data-aos="fade-up" data-aos-duration="1000")
        .speakerImg(:style="{'background-image': 'url(' + speaker.img + ')'}")
        .staffListContentBlock
            h2.staffListName {{speaker.name}}
            .staffListTitle(v-html="speaker.agency")
            h3 {{speaker.keynote}} :
            p {{speaker.content}}
            h3(v-if="speaker.summary!=''") Summary :
            .HTMLContainer(v-html="speaker.summary")
            h3(v-if="speaker.biography!=''") Biography :
            .HTMLContainer(v-html="speaker.biography")
</template>

<script>

export default {
    props: ['year'],
    data () {
        return {
            name: "KEYNOTES",
            speakers: {
                2023: [
                {
                    img: "https://mimsvai.github.io/static/imgs/Ashutosh_Dhekne_headshot.jpg",
                    name: "Ashutosh Dhekne",
                    agency: "Georgia Institute of Technology, USA",
                    keynote: "Keynote 1",
                    content: "Through a UWB Looking Glass: Localization, Orientation, and Gestures",
                    summary: '<p>The ability to accurately measure distances using radio-frequency signals opens up new possibilities for the world of VR, AR, and mixed reality. In this talk we try to address common problems in the VR/AR space via the use of ultra-wideband (UWB) radios, with support from inertial sensors when needed. We first introduce an infinitely scalable UWB-based indoor localization system that allows determining ones indoor location in a GPS-like privacy preserving fashion. As a fundamental primitive, this allows multiple VR headsets and hand-held controllers to localize themselves in an indoor space. Similarly, real-life objects that can be manipulated in the VR world, can also benefit from self-localization simply using a UWB and potentially an IMU sensor. We then think about facing direction with respect to the torso as a distance measurement problem (with funny looking head-gear). Finally, we address capturing body gestures using wearable sensors that measure inter-appendage distances. In short, this talk is about exploring new ways of obtaining vital information necessary for immersive VR and AR systems, without relying on outward facing cameras. From an accuracy perspective, wireless ranging is not there yet, and cannot compete with visual systems, as yet. Thus, this talk is also an invitation for the research community to look closely at wireless distance measurement primitives and improve the state-of-the-art. Wireless ranging and sensing offers benefits such as ability to operate in dark-rooms, bright featureless rooms, operate from behind cardboard props, operate in fog and other environments, which all promise an immersive experience for VR/AR consumers. Finally, wireless ranging offers a localization solution with negligible compute load.</p>',
                    biography: '<p><a href="https://faculty.cc.gatech.edu/~dhekne/" target="_blank">Dr. Ashutosh Dhekne</a> is an assistant professor at the School of Computer Science at Georgia Tech. His research interests span mobile computing and IoT, and is particularly interested in wireless localization, wireless sensing, and wireless networking. He obtained Ph.D. from the University of Illinois at Urbana Champaign, Master in Technology from IIT Bombay, and Bachelors from the University of Pune. Dr. Dhekne recently received the NSF Career award for advancing the state-of-the-art in UWB research. He received the Richard T. Cheng Endowed Fellowship as a Ph.D. student, and is an NTSE scholar.</p>'
                },
                {
                    img: "https://mimsvai.github.io/static/imgs/Pan%20Hao.jpg",
                    name: "Hao Pan",
                    agency: "Microsoft Research Asia",
                    keynote: "Keynote 2",
                    content: "Enhancing Wireless Communication Performance in VR/AR Scenarios Based on Metasurface Technology",
                    summary: '<p>In virtual reality (VR) and augmented reality (AR) applications, wireless communication plays a crucial role in ensuring high-quality, low-latency, and high-bandwidth data transmission. To meet the increasingly stringent performance requirements, this keynote speech will explore the use of metasurface technology to enhance wireless communication performance in VR/AR scenarios.</p><p>First, we will emphasize the importance of wireless communication in VR/AR contexts. Factors such as real-time response, low latency, high bandwidth, and multi-user support are essential for providing an immersive and smooth user experience. Moreover, communication security and stability become particularly important in critical applications such as healthcare, military, and education.</p><p>Next, we will discuss in detail the technical aspects of enhancing VR/AR communication performance based on metasurface technology. These aspects include adaptive beamforming, high-speed data transmission, and stealth communication technology. These techniques will help improve communication quality, speed, security, and stability, ultimately offering a superior virtual experience for users.</p><p>Finally, we will present several application cases of wireless communication performance enhancement in VR/AR scenarios based on metasurface technology and discuss the potential of this technology in future developments. With the continuous maturation and optimization of metasurface technology, we can expect to achieve higher performance and lower cost VR/AR devices, further driving the advancement of this field.</p><p>Through this keynote speech, we aim to provide the audience with an in-depth understanding of the application and development prospects of metasurface technology in wireless communication performance in VR/AR scenarios, stimulate innovative thinking, and jointly promote the development of VR/AR technology.</p>',
                    biography: '<p>Pan Hao joined Microsoft Research Asia (Shanghai) as a Researcher in 2022. Before that, he received his Ph.D. in computer science at the Shanghai Jiao Tong University (SJTU) in 2022, and completed undergraduate studies in the Yingcai Honors College of University of Electronic Science and Technology of China (UESTC) in 2016. His research interests focus on networked systems and span the areas of wireless communication and sensing, human-computer interaction, and computer vision.</p>'
                },
            ],
                2022: [
                {
                    img: "https://mimsvai.github.io/static/imgs/Shao-Yi%20Chien.jpeg",
                    name: "Shao-Yi Chien",
                    agency: "National Taiwan University, Taiwan <br>  <br> Ganzin Technology, Inc.",
                    keynote: "Keynote 1",
                    content: "Eye Tracking, the Core User Interface for the Metaverse",
                    summary: '<p>Metaverse is viewed as the next generation of the Internet and is the next big wave in the IT industry. Virtual Reality (VR) and Augmented Reality (AR) headsets will become the new major edge devices, bringing users to immerse into the Internet or fusing the digital and the physical worlds. In this talk, the founder and CEO of Ganzin Technology, Dr. Shao-Yi Chien, will present his point of view about why eye tracking will become the core user interface for AR/VR devices, and the immersive user experience will be achieved by integrating eye tracking with other user interaction subsystems as a multi-modality user interface. Dr. Chien will also show the achievements of Ganzin Technology---the most easy-to-integrate eye tracking solution on the market.</p>',
                    biography: '<p><a href="http://www.ee.ntu.edu.tw/profile1.php?id=101" target="_blank">Shao-Yi Chien</a> received the Ph.D. degree from the Department of Electrical Engineering, National Taiwan University (NTU), Taipei, Taiwan, in 2003. In 2004, he joined the Graduate Institute of Electronics Engineering and Department of Electrical Engineering, National Taiwan University, as an Assistant Professor. Since 2012, he has been a Professor. Prof. Chien served as the Chair of IEEE Circuits and Systems Society Multimedia Systems and Applications Technical Committee in 2017&mdash;2019. Dr. Chien is an expert in AR/VR, eye tracking, computer vision, real-time image/video processing, video coding, computer graphics, and system-on-a-chip design. He has published more than 300 papers and granted more than 40 patents. Since 2018, he is the founder and CEO of Ganzin Technology, Inc., an eye tracking solution provider for AR/VR/smart-glasses, which is the most easy-to-integrate solution on the market.</p>'
                },
                {
                    img: "http://www.mehdi-ammi.eu/images/mehdi.jpg",
                    name: "Mehdi Ammi",
                    agency: "University Paris 8, French",
                    keynote: "Keynote 2",
                    content: "Social haptic communication and interaction in Metaverse",
                    summary: '<p>The presentation deals with a series of research that address both the expression and perception of emotions with the haptic channel for different type of interpersonal interaction and communication. Our early research explored how people express physically emotions when communicating with 3D avatars. Then, we investigated how the haptic feedback supplements the facial expression of 3D avatars to improve their emotional expressivity and to remove the ambiguity of perception of some close emotions. In this context, we used new methods based the Information Integration Theory to study and model the way in which people combine haptic and visual information to perceive emotions. With the same methodology, we investigated other interpersonal communication configurations, in particular, Human-Robot Social Interaction. The objective was to improve the social capacities of robot in daily life (handshake, etc.).</p>',
                    biography: '<p><a href="http://www.mehdi-ammi.eu" target="_blank">Mehdi Ammi</a> is Professor at the University of Paris 8. He is engineer in electronics (2000), PhD in robotics (2005) and habilitaded to supervise research (HDR) in computer science since 2013. His research focuses on the study of VR-AR and pervasive environments while considering their societal and industrial applications (e-health, factory of the future, etc.). He addresses this field with a multidisciplinary approach combining artificial intelligence (machine learning, neurosciences, etc.), mechatronic technologies (organic electronics, smart textiles, etc.) and human factors (psychology, behavioral analysis, etc.). Mehdi Ammi was the leader or coleader of several international working groups (IEEE TCH, EuroVR Haptic SIG, etc.), and was involved in more than 20 national &amp; international projects and industrial collaborations.</p><p>For more info: see<a href="https://mikechen.com/" target="_blank"> </a><a href="http://www.mehdi-ammi.eu">http://www.mehdi-ammi.eu</a></p>'
                }
            ],
                2021: [
                {
                    img: "http://plopes.org/wp-content/uploads/2019/05/PedroLopes_UChicago2.jpg",
                    name: "Pedro Lopes",
                    agency: "University of Chicago, USA",
                    keynote: "Keynote 1",
                    content: "Human Computer Integration: Powering a New Generation of Interactive Devices",
                    summary: '<p><span style="font-weight: 400;">When we look back to the early days of computing, user and device were distant, often located in separate rooms. Then, in the &rsquo;70s, personal computers &ldquo;moved in&rdquo; with users. In the &rsquo;90s, mobile devices moved computing into users&rsquo; pockets. More recently, wearable devices brought computing into constant physical contact with the user&rsquo;s skin. These transitions proved useful: moving closer to users allowed interactive devices to sense more of their user and act more personal. The main question that drives my research is: </span><strong>what is the next interface paradigm that supersedes wearable devices?</strong></p><p><span style="font-weight: 400;">The primary way researchers have been investigating this is by asking where future interactive devices will be located with respect to the user&rsquo;s body. Many posit that the next generation of interfaces will be implanted inside the user&rsquo;s body. However, I argue that their location with respect to the user&rsquo;s body is not the primary factor; in fact, implanted devices are already happening in that we have pacemakers, insulin pumps, etc. Instead, I argue that the key factor is how will devices </span><strong>integrate</strong><span style="font-weight: 400;"> with the user&rsquo;s biological senses and actuators.&nbsp;</span></p><p><span style="font-weight: 400;">This body-device integration allows us to engineer interactive devices that intentionally borrow parts of the body for input and output, rather than adding more technology to the body. For example, one such type of body-integrated devices, which I have advanced recently, are interactive systems based on electrical muscle stimulation. These devices are able to move their user&rsquo;s muscles using computer-controlled electrical impulses, achieving the functionality of robotic exoskeletons without the bulky motors.&nbsp;</span></p><p><span style="font-weight: 400;">They key insight of my research is that engineering devices that intentionally borrow parts of the user&rsquo;s biology puts forward a </span><strong>new</strong> <strong>generation of miniaturized devices</strong><span style="font-weight: 400;">; allowing us to circumvent traditional physical constrains. For instance, in the case of our devices based on electrical muscle stimulation, they demonstrate how our body-device integration circumvents the constrains imposed by ratio of electrical power and size of a motor (i.e., the stronger/larger a motor is, more current needed to actuate it). Similarly, we demonstrate how our body-device integration approach </span><strong>even allowed us to miniaturize thermal feedback</strong><span style="font-weight: 400;"> (hot/cold sensations) without the need for power-hungry devices like Peltiers, air conditioners or heaters.</span></p>',
                    biography: '<p><a href="https://lab.plopes.org/"><span style="font-weight: 400;color: rgb(17, 85, 204);text-decoration: underline;">Pedro Lopes</span></a><span style="font-weight: 400;"> is an Assistant Professor in Computer Science at the University of Chicago, where he leads the </span><strong>Human Computer Integration lab</strong><span style="font-weight: 400;">. Pedro focuses on </span><strong>integrating computer interfaces with the human body&mdash;exploring the interface paradigm that supersedes wearable computing</strong><span style="font-weight: 400;">. Some of these new integrated-devices include: a device based on muscle stimulation that allows </span><a href="https://www.youtube.com/watch?v=Gz4dphzBb6I"><span style="font-weight: 400;color: rgb(17, 85, 204);    text-decoration: underline;">users to manipulate tools they never seen before</span></a><span style="font-weight: 400;"> or that </span><a href="https://www.youtube.com/watch?v=1BT8REEJibM"><span style="font-weight: 400;color: rgb(17, 85, 204);text-decoration: underline;">accelerate their reaction time</span></a><span style="font-weight: 400;">, or a device that </span><a href="https://www.youtube.com/watch?v=pH68GNkb_fA&amp;feature=youtu.be"><span style="font-weight: 400;color: rgb(17, 85, 204); text-decoration: underline;">leverages the sense of smell to create an illusion of temperature</span></a><span style="font-weight: 400;">. </span><span style="font-weight: 400;">Pedro&rsquo;s work is published at top-tier conferences (ACM CHI &amp; ACM UIST), where he has received four Best Paper awards, two Best Paper nominations and several Best Talk/Demo/Video awards. Pedro&rsquo;s work also captured the interest of media, such as New York Times, MIT Technology Review, NBC, Discovery Channel, NewScientist, Wired and has been shown at Ars Electronica and World Economic Forum (More: </span><a href="https://lab.plopes.org/"><span style="font-weight: 400;color: rgb(17, 85, 204);text-decoration: underline;">https://lab.plopes.org</span></a><span style="font-weight: 400;">)</span></p>'
                },
                {
                    img: "https://pbs.twimg.com/profile_images/502132318354419713/UXyxLGjS_400x400.jpeg",
                    name: "Mike Chen",
                    agency: "National Taiwan University, Taiwan",
                    keynote: "Keynote 2",
                    content: "Playing with Perception - Haptics Re-imagined",
                    summary: '<p><span style="font-weight: 400;">Motion simulator was invented over 100 years ago and modern haptic feedback devices were commercialized over 50 years ago. In this talk, we will take a fresh look at the fundamental limitations that have been in place ever since, and re-imaging how these experiences can be perceptually designed to be more realistic, immersive, and enable greater mobility.</span><span style="text-decoration: underline;"><span style="color: #3366ff;"><a style="color: #3366ff;" href="https://dl.acm.org/doi/10.1145/3386569.3392482"> <span style="font-weight: 400;">HeadBlaster</span></a></span></span><span style="font-weight: 400;">, is the first wearable approach to recreating the persistent proprioception and vestibular stimulation during real acceleration, enabling it to provide significantly longer motion sensation than motion platforms.</span><span style="text-decoration: underline;"><span style="color: #3366ff;"><a style="color: #3366ff; text-decoration: underline;" href="https://dl.acm.org/doi/abs/10.1145/3313831.3376292"> MiniatureHaptics</a></span></span><span style="font-weight: 400;"> explores whether haptic feedback can be remapped to just the hand, in order to enable new haptic experiences that are not practical to create at the full, human-body scale.&nbsp;&nbsp;</span></p><p><span style="font-weight: 400;">We will further explore research opportunities in applying perceptual design to haptic feedback, such as a multi-sensory approach that utilizes noise, which is normally undesir</span><span style="font-weight: 400;">able, to enhance haptic experiences, as well as perceptual approaches to address limitations of existing force feedback technologies. Last, we will discuss fun research and design opportunities by shifting focus from reproducing real-world experiences to out-of-this-world experiences. </span></p>',
                    biography: '<p><a href="https://mikechen.com/"><span style="font-weight: 400;"><span style="text-decoration: underline;"><span style="color: #3366ff; text-decoration: underline;">Prof. Mike Y. Chen</span></span></span></a><span style="font-weight: 400;"> creates future user experiences, and innovates at the intersection of HCI, AI, design, perception, and VR/AR. He founded the</span><span style="text-decoration: underline;"><span style="color: #3366ff;"><a style="color: #3366ff;" href="https://ntuhci.org/"> <span style="font-weight: 400;">HCI Lab</span></a></span></span><span style="font-weight: 400;"> at National Taiwan University, which has published at premier ACM conferences including CHI, SIGGRAPH, and UIST, and received Best Talk/Paper and Innovative Game Design Awards. He has</span><span style="text-decoration: underline;"><span style="color: #3366ff; text-decoration: underline;"><a style="color: #3366ff; text-decoration: underline;" href="https://github.com/ntu-hci-lab"> <span style="font-weight: 400;">open-sourced projects</span></a></span></span><span style="font-weight: 400;"> ranging from hardware designs of haptic systems to touchscreen gesture data analytics for ResearchKit, which was mentioned at Apple WWDC 2019.</span></p><p><span style="font-weight: 400;">Mike received his Ph.D. in Computer Science from UC Berkeley, with a Management of Technology (MOT) certificate from Haas School of Business. Prior to NTU, Mike led Mobile R&amp;D at Intel Research and startup Ludic Labs.&nbsp;</span></p><p><span style="font-weight: 400;">For more info: see</span><span style="text-decoration: underline;"><span style="color: #3366ff;"><a style="color: #3366ff;" href="https://mikechen.com/"> <span style="font-weight: 400;">https://mikechen.com/</span></a></span></span></p>'
                }
            ]
            }
        }
    },
    methods: {

    }
}
</script>

<style lang="sass" scoped>
@import "@/style/value.sass"
.speakerContainer 
    display: flex
    flex-wrap: nowrap
    margin: 40px 0
.speakerImg 
    width: 300px
    height: 240px
    background-position: center
    background-size: cover

.staffListTitle
    font-size: 19px
    width: 100%
    font-family: 'Roboto', sans-serif

.staffListName
    font-weight: bold
    width: 100%
    font-family: 'Roboto', sans-serif
    color: #e8b279
    font-weight: bold
    margin-top: 0

.staffListContentBlock
    margin-left: 40px
    padding-top: 10px

@media (max-width: 960px)
    .staffListContentBlock
        padding-top: 15px
        margin-top: 0 !important

</style>



