                    <li>Domain-specific VR/AR applications.</li>
                    <li>Design process, platform/tools, or libraries used to incorporate input modalities or sensations for VR/AR interactions or studying their use</li>
                </ul>

                <h1>Important Dates</h1>

                <p>Paper submission: 23:59 (AoE), <s>June 2, 2023</s> <s>June 9, 2023</s> June 16, 2023 <span style="color:#e74c3c">(extended and final)</span></p>

                <p>Paper notification: 23:59 (AoE), <s>June 30, 2023</s> July 8, 2023 <span style="color:#e74c3c">(extended)</span></p>

