                <p>Workshop: October 9th, 2023</p>

                <h1>Best Paper Award</h1>

                <p>The Best Paper Award is given to the best paper presented at the MIMSVAI 2023 workshop, to acknowledge and encourage excellence in research. The awardee will be presented with a certificate at the workshop.</p>

                <h2>Submission Guidelines</h2>

                <p>Submissions should be made electronically through the&nbsp;<a href="https://new.precisionconference.com/" target="blank">Online Submission System (PCS)</a><strong>. </strong>Please select &ldquo;SIGCHI&rdquo; as Society, &ldquo;Ubicomp/ISWC 2023&rdquo; as Conference/Journal, and &ldquo;UbiComp/ISWC 2023 Workshop MIMSVAI&rdquo; as the track on the submission page.<br />
                <br />
                <strong><span style="color:#e74c3c">All papers need to be anonymized</span>.</strong> Please submit papers with a maximum length of 5 pages (4-page + 1 references) in ACM SIGCHI sigconf template with 2 columns (<a href="https://www.acm.org/publications/proceedings-template">https://www.acm.org/publications/proceedings-template</a>). Please contact us (<a href="mailto:ubicomp.mimsvai@gmail.com">ubicomp.mimsvai@gmail.com</a>) if you have any problems when preparing your submissions.</p>

                <p>At least one author of each accepted paper needs to register for the conference and the workshop itself. During the workshop, each paper will be presented briefly by one of the authors. And, the Best Paper Award will be given to the best paper presented at the MIMSVAI 2023 workshop, to acknowledge and encourage excellence in research. The awardee will be presented with a certificate at the workshop.</p>

                <p>The accepted papers will be published in the UbiComp/ISWC Adjunct Proceedings, which will be included in the ACM Digital Library as part of the UbiComp conference supplemental proceedings.</p>
                `,
