                <p>Submissions should be made electronically through the&nbsp;<a href="https://new.precisionconference.com/" target="blank">Online Submission System (PCS)</a><strong>. </strong>Please select &ldquo;SIGCHI&rdquo; as Society, &ldquo;Ubicomp/ISWC 2022&rdquo; as Conference/Journal, and &ldquo;UbiComp/ISWC 2022 Workshop MIMSVAI&rdquo; as the track on the submission page.<br />
                <br />
