                <p>At least one author of each accepted paper needs to register for the conference and the workshop itself. During the workshop, each paper will be presented briefly by one of the authors. And, the Best Paper Award will be given to the best paper presented at the MIMSVAI 2022 workshop, to acknowledge and encourage excellence in research. The awardee will be presented with a certificate at the workshop.</p>

                <p>The accepted papers will be published in the UbiComp/ISWC Adjunct Proceedings, which will be included in the ACM Digital Library as part of the UbiComp conference supplemental proceedings.</p>
                `,
                2021: `<p dir="ltr">With the advance of VR/AR technology, more and more VR/AR&nbsp;applications are emerging and have been popular among new users.&nbsp;To facilitate user interaction with the systems, multi-modal input&nbsp;modalities are required. Furthermore, coherent integration of&nbsp;multiple realistic sensations can increase the level of realism, and&nbsp;therefore creates a truly immersive VR/AR experience. However,&nbsp;a lack of robust and intuitive interaction interfaces and realistic&nbsp;sensations hinders users&rsquo; experience for achieving a fascinating&nbsp;acceptance in various application areas of VR/AR interactions. This&nbsp;workshop discusses the challenges and applications of designing a&nbsp;higher coherence between the different input modalities and sensations&nbsp;to provide more engaging VR/AR experiences by leveraging&nbsp;the advances of both VR/AR software and hardware to improve user experience.</p>

                <h1 dir="ltr">Topics</h1>

                <p>Papers may include, but not be limited to, topics such as:</p>

                <ul>
                    <li>2D/3D and volumetric display and projection technology</li>
                    <li>Immersive analytics and visualization</li>
                    <li>Modeling and simulation</li>
                    <li>Multimodal capturing and reconstruction</li>
                    <li>Scene description and management issues</li>
                    <li>Storytelling</li>
                    <li>Tracking and sensing</li>
                    <li>Audio interfaces, sound rendering, spatialized audio, auditory perception and psychoacoustics</li>
                    <li>Embodied agents, virtual humans and (self-)avatars</li>
                    <li>Haptic and tactile interfaces, wearable haptics, passive haptics, pseudo haptics, other touch-based UI</li>
                    <li>Mediated and diminished reality</li>
                    <li>Multimodal input and output</li>
                    <li>Multisensory rendering, registration, and synchronization</li>
                    <li>Perception and cognition</li>
                    <li>Presence, body ownership, and agency</li>
