                    <li>Domain-specific VR/AR applications.</li>
                    <li>Design process, platform/tools, or libraries used to incorporate input modalities or sensations for VR/AR interactions or studying their use</li>
                </ul>

                <h1 dir="ltr">Important Dates</h1>

                <p dir="ltr">Paper submission: 23:59 (AoE), <s>June 15, 2021&nbsp;June 22, 2021</s> June 30, 2021 <span style="color:#e74c3c">(extended and final)</span></p>

                <p dir="ltr">Paper notification: 23:59 (AoE), July 15, 2021</p>

                <p dir="ltr">Camera-ready: 23:59 (AoE), <s>July 31, 2021 Aug. 6, 2021</s> Aug. 10, 2021<span style="color:#e74c3c"> (extended)</span></p>

                <p dir="ltr">Workshop: September 26, 2021</p>

                <h1 dir="ltr">Best Paper Award</h1>

                <p dir="ltr">The Best Paper Award is given to the best paper presented at the MIMSVAI 2021 workshop, to acknowledge and encourage excellence in research. The awardee will be presented with a certificate at the workshop.</p>

                <h2 dir="ltr">Submission Guidelines</h2>

                <p dir="ltr">Please submit your paper via this EasyChair link:<br />
                <a href="https://easychair.org/conferences/?conf=mimsvai2021">https://easychair.org/conferences/?conf=mimsvai2021</a></p>

                <p dir="ltr"><strong>All papers need to be anonymized.</strong> Please submit papers with a maximum length of 5 pages (4-page + 1 references) in ACM SIGCHI Master Article template with 2 columns (<a href="https://ubicomp.org/ubicomp2022/cfp/template-information/" target="_blank"></a>). Please contact us (<a href="mailto:ubicomp.mimsvai@gmail.com">ubicomp.mimsvai@gmail.com</a>) if you have any problems when preparing your submissions.</p>

                <p dir="ltr">At least one author of each accepted paper needs to register for the conference and the workshop itself. During the workshop, each paper will be presented briefly by one of the authors. And, the Best Paper Award will be given to the best paper presented at the MIMSVAI 2021 workshop, to acknowledge and encourage excellence in research. The awardee will be presented with a certificate at the workshop.</p>

                <p dir="ltr">The accepted papers will be published in the UbiComp/ISWC Adjunct Proceedings, which will be included in the ACM Digital Library as part of the UbiComp conference supplemental proceedings.</p>
                `
